<template>
	<div>
	<el-button id="butConnect" @click="clickConnect()">Record</el-button>
	<!-- <pre id="log"></pre> -->
</div>
</template>

<script>
	export default {
		data() {
			return {
        port: null,
        inputDone: null,
        reader: null,
      }
		},
		methods: {
			async connect() {
  				this.port = await navigator.serial.requestPort();
  				await this.port.open({ baudrate: 9600 });
  				let decoder = new TextDecoderStream();
  				this.inputDone = this.port.readable.pipeTo(decoder.writable);
  				let inputStream = decoder.readable;
  				this.reader = inputStream.getReader();
  				this.readLoop(this.reader);
			},

      async disconnect() {
        if (this.reader) {
          await this.reader.cancel();
          await this.inputDone.catch(() => {});
          this.reader = null;
          this.inputDone = null;
        }
        await this.port.close();
        this.port = null;
      },

			async clickConnect() {
				if (this.port) {
  				await this.disconnect();
  				this.toggleUIConnected(false);
  				return;
				}
  				await this.connect();
  				this.toggleUIConnected(true);
			},

			async readLoop(r) {
  				const log = document.getElementById("log");
  				while (true) {
    				const { value, done } = await r.read();
    				if (value) {
      					// log.textContent = value;
                this.$emit('newData', value)
    				}
    				if (done) {
      					console.log("[readLoop] DONE", done);
      					r.releaseLock();
      					break;
    				}
  				}
			},

			toggleUIConnected(connected) {
				const butConnect = document.getElementById("butConnect");
  				let lbl = "Record";
  				if (connected) {
    				lbl = "Stop";
  				}
  				butConnect.textContent = lbl;
			}
		}
	}
</script>