<template>
  <div id="chart">
    <div id="uv">
      <p id="title">UVI</p>
      <p id="number">{{uvData | optimize}}</p>
    </div>
    <div id="apex">
      <apexchart type="line" height="350" ref="chart" :options="chartOptions" :series="series"></apexchart>
    </div>
  </div>
</template>

<script>
  import VueApexCharts from 'vue-apexcharts'

  let data = [];
  let lastDate = 0;
  function getDayWiseTimeSeries(baseval, count) {
  var i = 0;
  while (i < count) {
    var x = baseval;
    var y = 0;
    data.push({
      x,
      y
    });
    lastDate = baseval;
    baseval += 1;
    i++;
  }
}

getDayWiseTimeSeries(0, 1);

function getNewSeries(baseval, uv) {
  var newDate = baseval + 1;
  lastDate = newDate;
  data.push({
    x: newDate,
    y: uv
  });
}

function resetData() {
  data = data.slice(data.length - 10, data.length);
}
	export default {
		props: ['uvData'],
    components: {
      'apexchart': VueApexCharts,
    },
		data() {
      return{
        uvD: null,
        series: [{
          data: data.slice()
        }],
        chartOptions: {
          chart: {
            id: 'realtime',
            height: 350,
            type: 'line',
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 500
              }
            },
            toolbar: {
              show: true
            },
            zoom: {
              enabled: false
            }
          },
          fill: {
            type: "gradient",
            gradient: {
              shadeIntensity: 1,
              opacityFrom: 0.7,
              opacityTo: 0.9,
              type: "vertical",
              colorStops: [
              {
                offset: 33.3,
                color: "#A465AA",
                opacity: 1
              },
              {
                offset: 53.3,
                color: "#EE3A12",
                opacity: 1
              },
              {
                offset: 66.6,
                color: "#F68501",
                opacity: 1
              },
              {
                offset: 86.6,
                color: "#FCF302",
                opacity: 1
              },
              {
                offset: 100,
                color: "#41AB24",
                opacity: 1
              },
              ]
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          title: {
            enabled: false
          },
          markers: {
            size: 0
          },
          xaxis: {
            range: 20,
            show: false
          },
          yaxis: {
            max: 15,
            show: false
          },
          legend: {
            show: false
          },
        },
      }
		},
    watch: {
      uvData() {
        this.uvD = this.uvData;
        // console.log(typeof(this.uvD) + this.uvD);
        if (this.uvD != null && this.uvD != '' && this.uvD < 20) {
          // console.log('here')
          getNewSeries(lastDate, this.uvD)
          this.$refs.chart.updateSeries([{
            data: data
          }])
        }
      }
    },
    mounted() {
    },
    filters: {
      optimize: function(value) {
        if (value > 15) {
          value = 0;
        }
        return value;
      }
    }
	}
</script>

<style>
  #chart {
    margin-top: 20px;
  }
  #title {
    font-size: 30px;
  }
  #number {
    font-weight: bold;
    font-size: 40px;
  }
  #uv {
    float: left;
    width: 200px;
    height: 350px;
    background-color: #EFEFF4;
  }
  #apex {
    margin-left: 200px;
  }
</style>